import { useState } from "react";
import { collection, addDoc, Timestamp } from "firebase/firestore";
import { db } from "../firebase";
import "../styles/quiz.css";

export default function QuizPage() {
  const [answers, setAnswers] = useState({});
  const [submitted, setSubmitted] = useState(false);

  const questions = [
    { id: 1, question: "React ka creator kaun hai?", options: ["Facebook", "Google", "Microsoft", "Amazon"] },
    { id: 2, question: "JavaScript ka extension kya hota hai?", options: [".java", ".js", ".py", ".jsx"] },
    { id: 3, question: "Next.js me routing kaise hoti hai?", options: ["pages folder", "routes array", "manual config", "CLI"] }
  ];

  const handleChange = (qid, option) => {
    setAnswers({ ...answers, [qid]: option });
  };

  const handleSubmit = async () => {
    if (Object.keys(answers).length < questions.length) {
      alert("Please answer all questions!");
      return;
    }
    try {
      await addDoc(collection(db, "quizResults"), {
        answers,
        createdAt: Timestamp.now()
      });
      setSubmitted(true);
    } catch (error) {
      console.error("Error saving quiz: ", error);
    }
  };

  if (submitted) {
    return <h2>âœ… Quiz Submitted! Thank you for participating.</h2>;
  }

  return (
    <div className="quiz-container">
      <h1>ðŸŽ¯ Daily Quiz Competition</h1>
      {questions.map((q) => (
        <div key={q.id} className="quiz-question">
          <p>{q.question}</p>
          {q.options.map((opt) => (
            <label key={opt}>
              <input
                type="radio"
                name={`q-${q.id}`}
                value={opt}
                onChange={() => handleChange(q.id, opt)}
              />
              {opt}
            </label>
          ))}
        </div>
      ))}
      <button onClick={handleSubmit} className="submit-btn">Submit</button>

      <div className="admin-login">
        <p>ðŸ”’ Admin? <a href="/admin-login">Login to see results</a></p>
      </div>
    </div>
  );
}
